/* ==========================================================================
   Border-box all the things!
   ========================================================================== */
*,
:before,
:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

/* ==========================================================================
   General Containers
   ========================================================================== */
html {
  margin: 0;
  padding: 0;
  font-family: "Helvetica", sans-serif; }

.wrapper {
  width: 90%;
  margin: 0 auto 1em;
  padding: 0;
  background: #fff;
  color: #444; }

.mainContent {
  padding: 0; }
  .mainContent:before,
  .mainContent:after {
    content: " ";
    /* 1 */
    display: table;
    /* 2 */ }
  .mainContent:after {
    clear: both; }

/* ==========================================================================
   Links
   ========================================================================== */
a,
a:visited {
  color: #FF8200;
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

/* ==========================================================================
   Images
   ========================================================================== */
img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

/* ==========================================================================
   Typography
   ========================================================================== */
h1 {
  font-size: 1.6em; }

p {
  margin-bottom: 1.5em; }

blockquote {
  margin: 0 1.5em; }

address {
  margin: 0 0 1.5em; }

/* ==========================================================================
   Alignment
   ========================================================================== */
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em; }

.aligncenter {
  clear: both;
  display: block;
  margin: 0 auto; }

/* ==========================================================================
   Pagination
   ========================================================================== */
.pagination {
  width: 90%;
  margin: 2em auto;
  padding: 0.3em 0;
  border-top: 1px dotted #000;
  list-style: none;
  font-size: 1.1em;
  text-align: center; }
  .pagination li {
    float: left;
    padding: 0 0.3em; }

/* ==========================================================================
   Accessiblity
   ========================================================================== */
/* Text meant only for screen readers */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important; }

.screen-reader-text:hover,
.screen-reader-text:active,
.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar */ }

/* Columnizer for three rows in the CMS */
.col-4 {
  clear: both;
  width: 100%;
  height: 100%; }

.col-3 {
  float: left;
  width: 75%;
  height: 100%; }

.col-2 {
  float: left;
  width: 50%;
  height: 100%; }

.col-1 {
  float: left;
  width: 25%;
  height: 100%; }

.alert {
  position: relative;
  padding: 1em;
  background: #f1f1f1;
  font-family: "Georgia", Times, serif;
  pointer-events: none; }
  .alert.denied {
